.message-list-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
  background-color: var(--bg-secondary);
  transition: background-color 0.3s ease;
  min-height: 0; // flex 자식 요소가 제대로 축소되도록
}

.channel-header {
  padding: 20px 24px;
  border-bottom: 1px solid var(--border-color);
  background: var(--slack-gradient);
  box-shadow: 0 2px 4px var(--shadow);
  transition: all 0.3s ease;
  flex-shrink: 0; // 헤더는 축소되지 않도록

  .channel-title {
    display: flex;
    align-items: center;
    gap: 10px;

    mat-icon {
      color: white;
      font-size: 24px;
      width: 24px;
      height: 24px;
    }

    h2 {
      margin: 0;
      font-size: 20px;
      font-weight: 700;
      color: white;
      letter-spacing: 0.3px;
    }
  }

  .channel-description {
    margin: 8px 0 0 34px;
    font-size: 13px;
    color: rgba(255, 255, 255, 0.9);
  }
}

.messages-scroll {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 20px 24px;
  background-color: var(--bg-primary);
  transition: background-color 0.3s ease;
  min-height: 0; // 스크롤이 제대로 작동하도록
  display: flex;
  flex-direction: column;
  gap: 12px;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--border-color);
    border-radius: 4px;

    &:hover {
      background-color: var(--text-muted);
    }
  }

  &::-webkit-scrollbar-track {
    background-color: var(--bg-secondary);
  }
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 48px 16px;
  gap: 16px;

  mat-spinner {
    ::ng-deep circle {
      stroke: var(--slack-primary);
    }
  }

  p {
    margin: 0;
    color: var(--text-muted);
    font-size: 14px;
  }
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 64px 16px;
  text-align: center;

  mat-icon {
    font-size: 64px;
    width: 64px;
    height: 64px;
    color: var(--slack-primary);
    opacity: 0.3;
    margin-bottom: 16px;
  }

  h3 {
    margin: 0 0 8px;
    font-size: 18px;
    font-weight: 700;
    color: var(--text-primary);
  }

  p {
    margin: 0;
    font-size: 14px;
    color: var(--text-muted);
  }
}
